@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Mono:100,400&display=swap');
$font-stack: 'IBM Plex Mono', -apple-system, BlinkMacSystemFont, "Open Sans", "Helvetica Neue", sans-serif;

$colorText: #fff;
$colorMain: #ff0000;

html {
    margin: 0;
    padding: 0;
    font-family: $font-stack;
    font-size: 10px;
    font-weight: 400;
    color: $colorText;
    height: 100%;
    background: #000;
    -webkit-text-size-adjust: 100%;
}

body {
    height: 100%;
    padding: 0;
    min-height: 100%;
    margin: 0;
    padding: 0;
    background: #000;
}

* {
    outline: none;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    outline: none;
}

.app {
    display: block;
    position: absolute;
    z-index: 100;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: hidden;
}